body, html {
  margin: 0 auto;
  min-width: 360px;
  max-width: 960px;
  height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 36px;
}

.container{
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.gametitle{
  width: 100%;
  margin: 0;
  text-align: center;
  margin-bottom: 2%;
  margin-top: 2%;
  font-size: 1em;
}
.board{
  margin: 0;
  -ms-flex-preferred-size: 80%;
      flex-basis: 80%;
  -ms-flex-negative: 1;
      flex-shrink: 1;

}
.gameinfo{
  text-align: center;
  margin: 0;
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  -ms-flex-negative:5;
      flex-shrink:5; 
  -webkit-transform: translateX(-100%); 
      -ms-transform: translateX(-100%); 
          transform: translateX(-100%);
  font-size: .6em;
}

.rowformat {
  width: 100%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.cell {
  border: 2px solid green;
  width: 5%;
  height: 0px;
  padding: 0;
  padding-bottom: 5%;
  margin: 0.1%;
  /* transition: all .2s linear; */
}

.pinkcell {
  border: 2px solid transparent;
  background-color: pink;
  border-radius: 50%;
  width: 5%;
  height: 0px;
  padding: 0;
  padding-bottom: 5%;
  margin: 0.1%;
  -webkit-transition: all .08s linear;
  -o-transition: all .08s linear;
  transition: all .08s linear;
}

.bluecell {
  border: 2px solid transparent;
  background-color: rgb(137, 243, 253);
  border-radius: 50%;
  width: 5%;
  height: 0px;
  padding: 0;
  padding-bottom: 5%;
  margin: 0.1%;
  -webkit-transition: all .08s linear;
  -o-transition: all .08s linear;
  transition: all .08s linear;
}


